import React from "react";
import Tarrifs from "../base/tarrifs.json";

class Block extends React.Component {
  state = {
    data: [{ name: "1" }, { name: "2" }],
  };
  componentDidMount() {
    let tarrifs = JSON.stringify(Tarrifs);
    this.setState({
      data: Tarrifs,
    });
  }
  render() {
    //console.log(this.state.data)
    return (
      <div className="cardbox">
        {this.state.data.map((item) => (
          <div className="card">
            <h4>{item.name}</h4>
            <div>{item.speed}</div>
            <div>{item.cost}</div>
            <div>{item.connect_cost}</div>
            <div>{item.quantity_TV_channels}</div>
            <div>{item.quantity_minutes}</div>
            <div>{item.quantity_Gb}</div>
            <div>{item.quantity_SMS}</div>
            {/* <div>{item.router.availability}</div>
            <div>{item.console}</div> */}
          </div>
        ))}
      </div>
    );
  }
}

export default Block;
